function Input({data}){

    const [inputValue, setInputValue] = useState('');
    const [inputValue2, setInputValue2] = useState('');

    const handleInputChange = (event) => {
        const valueWithoutSpaces = event.target.value.replace(/\s/g, '');
        setInputValue(valueWithoutSpaces);
      };

    const handleInputChange2 = (event) => {
        const valueWithoutSpaces2 = event.target.value.replace(/\s/g, '');
        setInputValue2(valueWithoutSpaces2);
      };

      
    return (
        <div className ="">
            <p className = "">{data.id}</p>
            <div className="">
                <div className={data.addClass+""}>
                    <input
                    type={data.inputType}
                    className={data.inputBox1}
                    name="username"
                    placeholder={data.id+" "}
                    value={inputValue}
                    onChange={handleInputChange}
                    required/>
                    <p class={data.at}>@</p>
                    <input
                    type={data.inputType}
                    className={data.inputBox2}
                    name="password"
                    placeholder={data.id+" "}
                    value={inputValue2}
                    onChange={handleInputChange2}
                    required/>
                    <button className={data.btn+" btnCheck"}></button>
                </div>
                <p className={data.addOn}>{data.err}</p>
            </div>
    </div>
    );
}

